@import "../../App.css";

.BodyWrapper {
  background-color: var(--white);
  margin-top: -10px;
}
.Body {
  background-color: rgb(255, 255, 255);
  align-self: center;
  width: 100%;
}
.Wrapper {
  padding-top: 150px;
  padding-bottom: 150px;
  width: 70vw;

  margin: 0 auto;
}


.ItemsCarouselWrapper {
  width: 90vw;

  padding-top: 30px;
  padding-bottom: 30px;
  margin: 0 auto;
  margin-top: -270px;
}
.CarouselCard {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
  overflow: hidden;
  height: 25vh;

  margin-top: 50px;
  margin-left: 50px;
  margin-right: 50px;

  background-color: var(--secondary);
  border-radius: 0.4rem;
  box-shadow: 0.2px 0.5px 0.5px rgba(0, 0, 0, 0.02),
    0.5px 1.1px 1.3px rgba(0, 0, 0, 0.028), 1px 2.1px 2.4px rgba(0, 0, 0, 0.035),
    1.8px 3.8px 4.2px rgba(0, 0, 0, 0.042),
    3.3px 7.1px 7.9px rgba(0, 0, 0, 0.05), 8px 17px 19px rgba(0, 0, 0, 0.07);
}

.CarouselCardActive {
  display: flex;
  align-items: flex-start;
  height: 30vh;
  width: 110%;
  overflow: hidden;
  margin-top: 20px;
  margin-bottom: 20px;

  margin-left: -5%;
  background-color: var(--secondary);
  border-radius: 1rem;
  box-shadow: 0.2px 0.5px 0.5px rgba(0, 0, 0, 0.02),
    0.5px 1.1px 1.3px rgba(0, 0, 0, 0.028), 1px 2.1px 2.4px rgba(0, 0, 0, 0.035),
    1.8px 3.8px 4.2px rgba(0, 0, 0, 0.042),
    3.3px 7.1px 7.9px rgba(0, 0, 0, 0.05), 8px 17px 19px rgba(0, 0, 0, 0.07);
}

.mainText {
  position: absolute;

  height: 30%;
  background-color: rgba(0, 0, 0, 0.61);
  width: 100%;
  color: var(--orange);
  font-size: 2.5em;
}
.CarouselPhoto {
  color: var(--white);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.CarouselPhoto img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}



