.AboutBackground .Wrapper {
  display: flex;
}

.About {
 
  width: 50%;
  height: auto;

  color: var(--white);
}
.About p {
  font-size: 1.7em;
}

.About h2{
    position: relative;
    font-size: 2em;
    font-weight: normal;
    padding-left: 20px;
    left: -25px;
    border-left: 5px solid var(--orange);
  
  }
.About button {
    position: relative;
    background: var(--orange);
    border-radius: 30px;
	color: var(--white);
	border: none;
    left: -5px;
    margin-top: 50px;
	padding: 13px 25px;
   
	font: inherit;
    font-weight:bold;
    font-size: 1.5em;
    
	cursor: pointer;
	outline: inherit;
  transition-duration: 0.5s;
}



.About button:hover {
  transform: translate(0px, -7px);
  background: var(--white);
  color: var(--orange);
  transition-duration: 0.5s;
}
.headshotContainerMobile{
  display: none;
}


.headshotContainerWeb{
  display: flex;
 
  height: 100%;
  min-width: 700px;
  width: 33%;
  padding-inline: 40px;
  align-items: center;
  justify-content:center;
  
}
.Headshot{
z-index: 10;
  position: absolute;
  height: auto;
  width: 50%;
  max-width: 500px;
  max-height: 500px;
  filter: drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.093));
}
.AboutBackground svg {
  position:absolute;
  height:auto;
  width: 50%;
  width: 760px;
  height: 760px;
  filter: drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.103));


}



@media (max-width: 900px) {
  h1 {
    font-size: 4.5em;
  }
  .Wrapper {
    padding-top: 20px;
    padding-bottom: 150px;
    width: 100vw;
    height: auto;
    margin: 0 auto;
  }


  .ItemsCarouselWrapper svg {
    display: none;
  }

  .CarouselCard,
  .CarouselCardActive {
    background-color: var(--secondary);
    border-radius: 0.4rem;
    box-shadow: 0 10px 20px -15px rgba(15, 72, 179, 0.2);
  }
  .CarouselCard {
    height: 250px;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 10px;
    margin-bottom: 0px;
  }
  .CarouselCardActive {
    visibility: hidden;
    height: 300px;
    width: 110%;
    margin-top: 0px;
    margin-left: 0%;
  }

  .ItemsCarouselWrapper {
    width: 100vw;
    margin: 0 auto;
  }


  .About{
      width: 90%;
      height: auto;
      margin:5px;
      margin-bottom: 30px;
      padding-left: 10px;
  }

  .About h2{
margin-left: 30px;}

  .Wrapper{
    height: 100%;
  

      }
  .AboutBackground{
    
  }
  .headshotContainerWeb{
    display: none;

    
  }
  .headshotContainerMobile{
    display: flex;
 
  height: 100%;
  width:100%;
  align-items: center;
  justify-content:center;
  padding-bottom: 90px;
  

    
  }

  .Headshot{
    z-index: 10;
      position: relative;
      height:auto;
      width: 70%;
   

    
    }
    .AboutBackground svg {
      position:absolute;
      height:auto;
      width: 95%;

    
    }
  .AboutBackground .Wrapper {
    display: flex;
    flex-direction: column;
  }
}
